// =======================================
// UI Kit 플러그인
// =======================================
@import "uikit/jiy-framework";
@import "lib/owl.carousel.min";
@import "lib/jquery.mCustomScrollbar";

// =======================================
// UI Kit 가이드 스타일
// =======================================
.ui-kit {
  padding: $base-line;
  background: #f3f3f3;
  overflow: hidden;
  h1 {
    font-size: 40px;
    color: #002847;
    letter-spacing: -0.03em;
    padding-top: 40px;
    .fa {
      font-size: 30px;
      vertical-align: 4px;
    }
  }
  strong {
    font-weight: 500;
    color: #000;
  }
  .markup-group {
    padding: 0 $base-line $base-line $base-line;
    background: #fff;
    margin-top: $base-line;
    border: 1px solid #e2e2e2;
    &:last-child {
      margin-bottom: $base-line;
    }
    .markup-title {
      color: #002847;
      padding-top: $base-line;
      padding-bottom: 10px;
      margin-bottom: 20px;
      border-bottom: 1px solid #e2e2e2;
    }
    .markup-sub-title {
      font-size: 20px;
      color: #69c;
      margin-top: 50px;
      margin-bottom: 10px;
      &:first-child {
        margin-top: 0;
      }
      .fa {
        font-size: 16px;
        margin-right: 6px;
        vertical-align: 2px;
      }
    }
  }
}

// =======================================
// 마크업 스킨 다운 스타일
// =======================================
.ui-kit {
  pre[class*="language-"] {
    position: relative;
    font-family: $font-family-ko;
    color: inherit;
    background: none;
    text-align: left;
    white-space: pre-wrap;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
    padding: 65px 30px 30px 30px;
    border-radius: 10px;
    background: #eff4f7;
    overflow: auto;
    &::-moz-selection,
     ::-moz-selection {
      text-shadow: none;
    }
    &:before {
      position: absolute;
      top: 20px;
      left: 30px;
      font-size: 20px;
      font-weight: bold;
      color: #002847;
    }
    &[class*="-html"] {
      &:before {
        content: "HTML";
      }
    }
    &[class*="-css"] {
      &:before {
        content: "CSS";
      }
    }
    &[class*="-js"] {
      &:before {
        content: "JS";
      }
    }
    .copy {
      position: absolute;
      top: 30px;
      right: 30px;
      font-size: 14px;
      color: #fff;
      padding: 2px 8px 4px 8px;
      border-radius: 6px;
      background: #bcd0dc;
      cursor: pointer;
      &:hover,
      &:focus {
        background: #002847;
      }
    }
  }
   :not(pre)>code[class*="language-"] {
    padding: .1em;
    border-radius: .3em;
    white-space: normal;
  }
  .token {
    &.comment, &.prolog, &.doctype, &.cdata {
      color: slategray;
    }
    &.punctuation {
      color: #999;
    }
    &.property, &.tag, &.boolean, &.number, &.constant, &.symbol, &.deleted {
      color: #905;
    }
    &.selector, &.attr-name, &.string, &.char, &.builtin, &.inserted {
      color: #690;
    }
    &.operator, &.entity, &.url {
      color: #a67f59;
    }
    &.atrule, &.attr-value, &.keyword {
      color: #07a;
    }
    &.function {
      color: #DD4A68;
    }
    &.regex, &.important, &.variable {
      color: #e90;
    }
    &.important, &.bold {
      font-weight: bold;
    }
    &.italic {
      font-style: italic;
    }
    &.entity {
      cursor: help;
    }
  }
  .language-css .token.string, .style .token.string {
    color: #a67f59;
  }
  .namespace {
    opacity: .7;
  }
}

.code-box-copy {
  position: relative;
  .code-box-copy__btn {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 12px;
    color: #fff;
    text-shadow: 0 0 1px #003359;
    padding: 2px 10px 6px 10px;
    border: 1px solid #003761;
    border-radius: 8px;
    background: #003761;
    z-index: 1000;
    &:before {
      content: "Copy";
      text-align: center;
    }
  }
}

.ui-kit-comment {
  padding: 15px;
  border: 10px solid #eff4f7;
  margin-top: -10px;
}

[class*="ui-kit"] {
  .red {
    color: #e51d68;
  }
  .blue {
    color: #257de0;
  }
}

// =======================================
// 타이포그라피
// =======================================
.typography-area {
  @extend %clearfix;
  padding: $base-line;
  .typography-wrap {
    padding: 0 10px;
    @include span(full);
    @include susy-breakpoint(em($tablet)) {
      @include span(2);
      &:last-child {
        @include last;
      }
    }
  }
  h6, h5, h4, h3, h2, p {
    margin-top: $base-line;
  }
  h3:first-child {
    margin-top: 0;
  }
  h6 {
    padding: 9px 0;
  }
  h5 {
    padding: 7px 0;
  }
  h4 {
    padding: 6px 0;
  }
  h3 {
    padding: 4px 0;
  }
  h2 {
    padding: 2px 0;
  }
  &:hover,
  &:focus {
    background: linear-gradient(transparent ($base-line - 1), rgba(205, 205, 205, 1) $base-line);
    background-size: 1px $base-line;
    h6, h5, h4, h3, h2, p {
      background: hsla(0, 0%, 0%, 0.2);
    }
  }
}

// =======================================
// 버튼 스타일
// =======================================
// =======================================
// Porgress 스타일
// =======================================
// =======================================
// 화살표 스타일
// =======================================
// =======================================
// 폰트어썸 활용 방법 스타일
// =======================================
.icon-fortawesome-before:before {
  font-family: FontAwesome;
  content: "\f286";
  margin-right: 4px;
}

.icon-fortawesome-after {
  color: #999;
  &:after {
    font-family: FontAwesome;
    font-size: 14px;
    color: #4d52de;
    content: "\f286";
    margin-left: 4px;
  }
}

.fortawesome-area {
  ol {
    padding: 0 20px 0 30px;
    li {
      list-style-type: decimal;
      margin-bottom: 10px;
      .link {
        text-decoration: underline;
        &:hover,
        &:focus {
          @extend .blue;
        }
        .fa {
          font-size: 12px;
          margin-right: 2px;
          color: #0a52a3;
        }
      }
    }
  }
}

// =======================================
// 테이블 스타일
// =======================================
.table-type {
  h2 {
    margin-top: 20px;
  }
  h3 {
    .fa {
      margin-right: 10px;
      vertical-align: -1px;
    }
  }
}